
.col-auto{
  max-width: 75%;
  padding: 0;
}
.justify-start{
  .img_col{
    display: flex;
    justify-content: flex-start;
    padding-left: 0px;
  }
}
.justify-end{
  .img_col{
    display: flex;
    justify-content: flex-end;
    padding-right: 0px;
  }
}

.img_col .v-card{
  line-height: 0 !important;
}
.img_col img{
  max-height: 450px;
}
.v-card__text {
  padding: 10px 15px;
  overflow-wrap: break-word;
  word-wrap:break-word;
  word-break:break-all;
}

.own-message {
  background-color: #000 !important;
  color: #fff;
  border-radius: 16px 16px 0 16px;
  margin: 5px 0;
  /* margin-right: 15px; */
}
.own-message .v-card__text{
  color: #FFF;
}

.other-message {
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 16px 16px 16px 0;
  margin: 5px 0;
}

.date-divider {
  color: #aaa;
}
img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  // margin-top: 10px;
  cursor: pointer;
}
